<template>
  <el-container>
    <el-header>
      <span class="return" @click="$router.back()">返回</span>
      <div class="right">
        <!-- <span class="home">首页</span> -->
        <!-- <span class="share">分享</span> -->
      </div>
    </el-header>
    <el-main>
      <div v-if="order">
        <strong>订单信息</strong>
        <p>订单编号： {{ order.order_sn}}</p>
        <p>订单金额： {{ order.order_amount}}</p>
      </div>
      <p><strong>选择你的付款方式</strong></p>
      <el-radio-group v-model="radio">
    <el-radio label="微信">微信</el-radio>
    <el-radio label="支付宝">支付宝</el-radio>
  </el-radio-group>
    </el-main>
    <el-footer>
      <!-- <span class="kefu">客 </span> -->
      <!-- <router-link to="/cart">购物车</router-link> -->
      <!-- <span class="collect">收藏 </span> -->
      <!-- <el-button type="danger" @click="addCart">加入购物车</el-button> -->
      <!-- <span class="totalAmount">总计 ： {{ cartTotal }} </span> -->
      <el-button type="warning" @click="pay">确认付款</el-button>
    </el-footer>
    <login-widget ref="login_widget"></login-widget>
  </el-container>
</template>

<script>
import server from "../server";
import LoginWidget from "./widget/Login";
import global from "../common/global";
export default {
  components: {
    LoginWidget
  },
  data() {
    return {
      order: [],
      radio: '微信',
    };
  },

  mounted() {
    this.getOrderDetail();
  },

  methods: {
    getOrderDetail: function() {
      var that = this;
      server
        .getOrderDetail(this.$route.params.orderId)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.order = $data.data;
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },

    pay : function(){
      var params = {payment:this.radio,order_id:this.$route.params.orderId};
      var that = this;
      server
        .pay(params)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.order = $data.data;
            that.$router.push(`/pay/success`);
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    }
  }
};
</script>
