<template>
  <el-container>
    <el-header>
      <span class="return" @click="$router.back()">返回</span>
      <div class="right">
        <span class="home">
          <router-link to="/address/add">添加</router-link>
        </span>
        <!-- <span class="share">分享</span> -->
      </div>
    </el-header>
    <el-main>
      <div v-if="list.length > 0" class="address_info">
        <div v-for="item in list" class="address">
          <router-link :to="{ name: 'submitOrder', params: { addressId: item.id }}">
            {{item.province}}{{item.city}}{{item.area}}{{item.town}}
            <br />
            <strong>{{item.address}}</strong>
            <br />
            {{item.name}} {{item.phone}}
          </router-link> 
          <hr>
        </div>
      </div>
      <div v-else>你还没有收货地址，请点击右上角的‘添加’按钮。</div>
    </el-main>
    <login-widget ref="login_widget"></login-widget>
  </el-container>
</template>

<script>
import server from "../server";
import LoginWidget from "./widget/Login";
export default {
  name: "goodsDetail",
  components: {
    LoginWidget
  },
  data() {
    return {
      list: []
    };
  },

  mounted() {
    this.getAddressList();
  },

  methods: {
    // 获取用户地址列表
    getAddressList: function() {
      var that = this;

      server
        .getAddressList()
        .then(function(response) {
          var $data = response.data;
          that.list = $data.data;
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    }
  }
};
</script>
<style scoped>
.el-header {
  text-align: left;
}
.right {
  display: inline;
  float: right;
}
.el-footer {
  position: fixed;
  line-height: 60px;
  text-align: right;
}
.el-main {
  margin-bottom: 60px;
}
.el-footer a {
  float: left;
}
.address{
  margin: 20px 0;
}
</style>