<template>
  <el-container>
    <el-header>
      <span class="return" @click="$router.back()">返回</span>
      <div class="right">
        <span class="home">首页 </span>
        <span class="share">分享</span>
      </div>
    </el-header>
    <el-main v-if="detail">
      <div v-if="detail.me.length > 1">
        <el-carousel height="200px">
          <el-carousel-item v-for="banner in detail.me">
            <img :src="banner" />
          </el-carousel-item>
        </el-carousel>
      </div>

      <div v-else-if="detail.me.length == 1">
        <el-image style="width:100%; height:150px" :src="detail.me[0]" fit="fill"></el-image>
      </div>

      <div v-else-if="detail.me.length == 0">
        <el-image style="width:100%; height:150px" :src="detail.image_uri" fit="fill"></el-image>
      </div>


      <div class="description">
        <label>商品名称：</label>{{detail.name}}<br>
        <label>商品市场价：</label>¥{{detail.line_price}}元<br>
        <label>商城价：</label>¥{{detail.price}}元<br>
        <label>商品描述：</label>
        <span v-html="detail.content"></span>
      </div>
      
    </el-main>

    <el-main v-else>
      暂无商品信息
    </el-main>
    <el-footer>
      <span class="kefu">客 </span>
      <router-link to="/cart">购物车 </router-link>
      <span class="collect">收藏 </span>
      <el-button type="danger" @click="addCart">加入购物车</el-button>
      <el-button type="warning">立即购买</el-button>
       
    </el-footer>
    <login-widget ref="login_widget"></login-widget>
  </el-container>
</template>

<script>
import server from "../server";
import LoginWidget from "./widget/Login";
export default {
  name:'goodsDetail',
  components: {
    LoginWidget
  },
  data() {
    return {
      detail:false
    };
  },

  mounted() {
    var that = this;
     // 获取商品详情
    server
      .getGoodsDetail(this.$route.params.goodsId)
      .then(function(response) {
        var $data = response.data;
        if ($data.code == 200) {
          that.detail = $data.data;

        }
      })
      .catch(function(error, data) {
        that.$message.error(error.response.data.message);
      });
  },

  methods: {
    
    addCart: function() {
      let $data = {
        goods_id: this.$route.params.goodsId
      };
      var that = this;
      server
        .addCart($data)
        .then(function(response) {
          var $data = response.data;
          console.log($data)
          
          if ($data.code == 200) {
            that.$message.success($data.data.msg);
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    }
  }
};
</script>
<style scoped>
.el-header {
  text-align: left;
}
.right {
  display: inline;
  float: right;
}
.el-footer{
  position: fixed;
  line-height: 60px;
}
.el-main{
  margin-bottom: 60px;
}
</style>