<template>
  <el-container>
    <el-header>
      <span class="return" @click="$router.back()">返回</span>
      <div class="right">
        <!-- <span class="home">首页</span> -->
        <!-- <span class="share">分享</span> -->
      </div>
    </el-header>
    <el-main>
      <el-form ref="form" :model="form" label-position="top">
        <el-form-item label="收货人">
          <el-input v-model="form.name" minlength="2"></el-input>
        </el-form-item>
        <el-form-item label="联系电话">
          <el-input v-model="form.phone" minlength="11" maxlength="11"></el-input>
        </el-form-item>
        <el-form-item label="地址选择">
          <el-cascader :props="props" @change="handlechange"></el-cascader>
        </el-form-item>
        <el-form-item label="详细地址">
          <el-input type="textarea" v-model="form.address" minlength="2"></el-input>
        </el-form-item>
        <el-form-item label="是否默认">
          <el-switch v-model="form.isDefault"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit" round>保存</el-button>
        </el-form-item>
      </el-form>
    </el-main>
    <login-widget ref="login_widget"></login-widget>
  </el-container>
</template>

<script>
import server from "../server";
import LoginWidget from "./widget/Login";
export default {
  name: "goodsDetail",
  components: {
    LoginWidget
  },
  data: {
    that: this
  },
  data() {
    return {
      form: {
        name: "",
        phone: "",
        address: "",
        isDefault: false,
        area: []
      },
      props: {
        lazy: true,
        lazyLoad(node, resolve) {
          var params = { params: {} };
          if (node.data) {
            params.params.pre_id = node.data.value;
          }
          server
            .getArea(params)
            .then(function(response) {
              var $data = response.data;
              if ($data.code == 200) {
                const nodes = Array.from($data.data).map(item => ({
                  value: item.area_id,
                  label: item.name,
                  leaf: !item.hasSub
                }));

                // 通过调用resolve将子节点数据返回，通知组件数据加载完成
                resolve(nodes);
              }
            })
            .catch(function(error, data) {
              // that.$message.error(error.response.data.message);
              window.vue.$message.error(error.response.data.message);
            });
        }
      }
    };
  },

  mounted() {},

  methods: {
    onSubmit: function() {
      // console.log("onSubmit!");
      // console.log("From Data:");
      // console.log(this.form);
      var that = this;
      server
        .addAddress(this.form)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.$message.success($data.data.msg);
            that.$router.back();
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },
    handlechange: function(value) {
      this.form.area = value;
    }
  }
};
</script>
<style scoped>
.el-header {
  text-align: left;
}
.right {
  display: inline;
  float: right;
}
.el-footer {
  position: fixed;
  line-height: 60px;
  text-align: right;
}
.el-main {
  margin-bottom: 60px;
}
.el-footer a {
  float: left;
}
</style>