// import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../components/Home'
import Login from '../components/common/Login'
import Register from '../components/common/Register'
import Reset from '../components/widget/Reset'
import QiNiu from '../components/widget/QiNiu'
import Vote from '../components/widget/Vote'
import GoodsDetail from '../components/GoodsDetail'
import Cart from '../components/Cart'
import SubmitOrder from '../components/SubmitOrder'
import AddressAdd from '../components/AddressAdd'
import AddressList from '../components/AddressList'
import Pay from '../components/Pay'
import PaySuccess from '../components/PaySuccess'
import User from '../components/User'
import Order from '../components/Order'

// Vue.use(VueRouter)

const routes = [
  { path: '/', component: Home },
  { path: '/home', component: Home },
  { path: '/login', component: Login },
  { path: '/register', component: Register },
  { path: '/reset', component: Reset },
  { path: '/qiniu', component: QiNiu, meta: { requireAuth: true } },
  { path: '/vote', component: Vote, meta: { requireAuth: true } },

  {
    path: '/goods/:goodsId/detail',
    name: 'goodsDetail',
    component: GoodsDetail
  },

  { path: '/cart', component: Cart, meta: { requireAuth: true } },
  { path: '/address/add', component: AddressAdd, meta: { requireAuth: true } },
  { path: '/address/list', component: AddressList, meta: { requireAuth: true } },
  { path: '/submit_order', component: SubmitOrder, meta: { requireAuth: true } },
  { path: '/order/:addressId/submit', name: 'submitOrder', component: SubmitOrder, meta: { requireAuth: true } },
  { path: '/order/:orderId/pay', name: 'Pay', component: Pay, meta: { requireAuth: true } },
  { path: '/pay/success', name: 'PaySuccess', component: PaySuccess },
  { path: '/user', name: 'User', component: User, meta: { requireAuth: true } },
  { path: '/order', name: 'Order', component: Order, meta: { requireAuth: true } },
  
]

// eslint-disable-next-line no-new
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  let whiteList = ['/login', '/home'];
  if (whiteList.indexOf(to.path)) {
    next()
  }
  if (to.meta.requireAuth == true) {
    if (sessionStorage.getItem('authToken')) {
      next()
    } else {
      next('/login')
    }
  }
  next()
})

export default router
