<template>
  <el-container>
    <el-header>
      <span class="return" @click="$router.back()">返回</span>

      订单列表
      
    </el-header>
    <el-main v-if="list.length > 0">
      <div class="cart_list">
        <ol>
            <li v-for="item in list">
              <div class="detail">
                <span class="name">订单编号：{{ item.order_sn }}</span>
                <br />
                <span class="price">状态：{{ item.order_status_desc }}</span>
                <br />
                <span class="price">金额：{{ item.order_amount }}</span>
                <br />
                <span class="time">时间：{{ item.created_at }}</span>
              </div>
            </li>
        </ol>
      </div>
    </el-main>

    <el-main v-else>暂无信息</el-main>
    <login-widget ref="login_widget"></login-widget>
  </el-container>
</template>

<script>
import server from "../server";
import global from "../common/global";
import LoginWidget from "./widget/Login";

export default {
  name: "goodsDetail",
  data() {
    return {
      list: false,
    };
  },
  components: {
    LoginWidget
  },

  mounted() {
    // 获取商品详情
    this.renderCart();
  },

  methods: {
    
    getImageUri: url => {
      return url ? global.basic_uri + url : "";
    },


    // 购物车列表渲染
    renderCart: function() {
      var that = this;
      server
        .getOrderList(this.$route.params.goodsId)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.list = $data.data.data;            
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },
  }
};
</script>
