    <el-carousel height="200px">
      <el-carousel-item v-for="banner in banners">
        <img :src="banner.image_uri" />
      </el-carousel-item>
    </el-carousel>

    <div class="goods_list">
      <ol>
        <li v-for="goods in goodsList">
          <router-link :to="{ name: 'goodsDetail', params: { goodsId: goods.id }}">
            <el-image style="width:100%; height:150px" :src="goods.image_uri" fit="fill"></el-image>
            <br />
            <span class="name">{{ goods.name }}</span>
            <span class="price">{{ goods.price }}</span>
            <span class="line_price">{{ goods.line_price }}</span>
          </router-link>
        </li>
      </ol>
    </div>

    </el-main>
    <Footer></Footer>
  </div>
</template>

<script>
import server from "../server";
import Header from "./common/Header";
import Footer from "./common/Footer";

export default {
  components: { Header, Footer },
  data() {
    return {
      banners: [],
      goodsList: [],
      fits: ["fill", "contain", "cover", "none", "scale-down"],
      url:
        "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
      
    };
    
  },

  mounted() {
    // Echo.channel('leisure_database_rss')
    //     .listen('RssCreatedEvent', (e) => {
    //         console.log(e)
    //     });

    // 获取首页banner
    var that = this;
    server
      .getBanner()
      .then(function(response) {
        var $data = response.data;
        if ($data.code == 200) {
          that.banners = $data.data;
        }
      })
      .catch(function(error, data) {
        that.$message.error(error.response.data.message);
      });

    // 获取商品列表
    server
      .getGoodsList()
      .then(function(response) {
        var $data = response.data;
        if ($data.code == 200) {
          that.goodsList = $data.data;
        }
      })
      .catch(function(error, data) {
        that.$message.error(error.response.data.message);
      });
  }
};
</script>
