<template>
  <el-container>
    <el-main v-if="user">
      <div class="header">
        <el-image
          style="width: 100px; height: 100px; border-radius: 100%;"
          circle
          :src="user.head_img"
        ></el-image>
        <div class="detail">{{user.name}}</div>
      </div>

      <div class="order">
        <router-link to="order">查看所有订单</router-link>
      </div>
    </el-main>
    <Footer></Footer>
    <login-widget ref="login_widget"></login-widget>
  </el-container>
</template>

<script>
import server from "../server";
import LoginWidget from "./widget/Login";
import global from "../common/global";
import Footer from "./common/Footer";
export default {
  components: {
    LoginWidget,Footer
  },
  data() {
    return {
      user: false
    };
  },

  mounted() {
    this.getProfile();
  },

  methods: {
    getProfile: function() {
      var that = this;
      server
        .profile()
        .then(function(response) {
          var $data = response.data;
          that.user = $data.data;
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    }
  }
};
</script>
