<template>
  <el-container>
    <el-header>
      <span class="return" @click="$router.back()">返回</span>
      <div class="right">
        <span class="delete" @click="handleDelete">删除</span>
      </div>
    </el-header>
    <el-main v-if="list.length > 0">
      <div class="cart_list">
        <ol>
          <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
            <li v-for="item in list">
              <div class="checkbox">
                <el-checkbox :label="item.id">&nbsp;</el-checkbox>
              </div>

              <router-link :to="{ name: 'goodsDetail', params: { goodsId: item.goods_id }}">
                <el-image
                  style="width:100px; height:100px"
                  :src="getImageUri(item.face_img)"
                  fit="fill"
                ></el-image>
              </router-link>

              <div class="detail">
                <span class="name">{{ item.goods_name }}</span>
                <br />
                <span class="price">价格：{{ item.member_price }}</span>
                <br />
                <span class="num">数量：{{ item.goods_num }}</span>
                <br />
                <span class="act_btn">
                  <el-button
                    type="success"
                    size="mini"
                    icon="el-icon-plus"
                    circle
                    @click="incr(item.id)"
                  ></el-button>
                  <el-button
                    type="danger"
                    size="mini"
                    icon="el-icon-minus"
                    circle
                    @click="desc(item.id)"
                  ></el-button>
                </span>
              </div>
            </li>
          </el-checkbox-group>
        </ol>
      </div>
    </el-main>

    <el-main v-else>暂无信息</el-main>
    <el-footer>
      <el-checkbox
        :indeterminate="isIndeterminate"
        v-model="checkAll"
        @change="handleCheckAllChange"
      >全选</el-checkbox>
      总计 ： {{ total }} 元
      <el-button type="warning" @click="submitOrder()">提交订单</el-button>
    </el-footer>
    <login-widget ref="login_widget"></login-widget>
  </el-container>
</template>

<script>
import server from "../server";
import global from "../common/global";
import LoginWidget from "./widget/Login";

export default {
  name: "goodsDetail",
  data() {
    return {
      list: false,
      total: 0,
      checkAll: false,
      checkedCities: [],
      isIndeterminate: false,
      cityOptions: []
    };
  },
  components: {
    LoginWidget
  },

  mounted() {
    // 获取商品详情
    this.renderCart();
  },

  methods: {
    handleCheckAllChange(val) {
      this.checkedCities = val ? this.cityOptions : [];
      this.isIndeterminate = false;
      this.cartCheck();
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.checkedCities.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.checkedCities.length;
      this.cartCheck();
    },
    getImageUri: url => {
      return url ? global.basic_uri + url : "";
    },

    // 购物车删除
    handleDelete: function() {
      var that = this;
      var data = { ids: this.checkedCities };
      server
        .cartDelete(data)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.$message.success($data.data.msg);
            that.renderCart();
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },

    // 购物车选中
    cartCheck: function() {
      var that = this;
      var data = { ids: this.checkedCities };
      server
        .cartChecked(data)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.total = $data.data.total_price;
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },

    // 商品数量+1
    incr: function($cartId) {
      var that = this;
      var data = { type: 1 };

      server
        .changeNum($cartId, data)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.$message.success($data.data.msg);
            that.renderCart();
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },

    // 商品数量-1
    desc: function($cartId) {
      var that = this;
      var data = { type: 2 };

      server
        .changeNum($cartId, data)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.$message.success($data.data.msg);
            that.renderCart();
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },

    // 购物车列表渲染
    renderCart: function() {
      var that = this;
      server
        .getCartList(this.$route.params.goodsId)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.list = $data.data.list.data;
            that.total = 0;
            for (const key of that.list) {
              that.cityOptions.push(key.id);
              if (key.is_checked === 1) {
                that.checkedCities.push(key.id);
                that.total +=
                  parseInt(key.goods_num) * parseFloat(key.member_price);
              }
            }
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },

    // 提交订单
    submitOrder: function() {
      if (this.checkedCities.length < 1) {
        this.$message.warning("请先勾选一个商品");
        return;
      }
      this.$router.push("/submit_order");
    },

    buyNow: function() {
      if (this.checkedCities.length < 1) {
        this.$message.warning("请先勾选一个商品");
      }
      var that = this;
      server
        .submitOrder()
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.renderCart();
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    }
  }
};
</script>
