<template>
  <el-container>
    <el-header>
      <span class="return" @click="$router.back()">返回</span>
      <div class="right">
        <span class="home">首页</span>
        <!-- <span class="share">分享</span> -->
      </div>
    </el-header>
    <el-main>
      <strong>收货信息</strong>
      <hr />
      <div v-if="address" class="address_info">
        <router-link to="/address/list">
          {{address.province}}{{address.city}}{{address.area}}{{address.town}}
          <br />
          <strong>{{address.address}}</strong>
          <br />
          {{address.name}} {{address.phone}}
        </router-link>
      </div>
      <div v-else>
        你还没有收货地址，请点击
        <router-link to="address/add">添加</router-link>。
      </div>
      <hr />
      <br />
      <strong>商品信息</strong>
      <hr />
      <div class="cart_list">
        <ol>
            <li v-for="item in cartList">
                <el-image
                  style="width:100px; height:100px"
                  :src="getImageUri(item.face_img)"
                  fit="fill"
                ></el-image>

              <div class="detail">
                <span class="name">{{ item.goods_name }}</span>
                <br />
                <span class="price">价格：{{ item.member_price }}</span>
                <br />
                <span class="num">数量：{{ item.goods_num }}</span>
                <br />
              </div>
            </li>
        </ol>
        <p class="cartTotal">小计 ： {{ cartTotal }}</p>
      </div>
      <hr />
    </el-main>
    <el-footer>
      <!-- <span class="kefu">客 </span> -->
      <router-link to="/cart">购物车</router-link>
      <!-- <span class="collect">收藏 </span> -->
      <!-- <el-button type="danger" @click="addCart">加入购物车</el-button> -->
      <span class="totalAmount">总计 ： {{ cartTotal }} </span>
      <el-button type="warning" @click="submitOrder">下单</el-button>
    </el-footer>
    <login-widget ref="login_widget"></login-widget>
  </el-container>
</template>

<script>
import server from "../server";
import LoginWidget from "./widget/Login";
import global from "../common/global";
export default {
  name: "goodsDetail",
  components: {
    LoginWidget
  },
  data() {
    return {
      address: [],
      cartList: [],
      cartTotal: 0
    };
  },

  mounted() {
    this.getAddressList();
    this.renderCart();
  },

  methods: {
    // 获取用户地址列表
    getAddressList: function() {
      var that = this;
      var params = { params: {} };
      if (this.$route.params.addressId) {
        params.params.id = this.$route.params.addressId;
      }

      server
        .getAddressList(params)
        .then(function(response) {
          var $data = response.data;
          if (that.$route.params.addressId) {
            that.address = $data.data;
          } else {
            that.address = $data.data[0];
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },

     getImageUri: url => {
      return url ? global.basic_uri + url : "";
    },
    // 购物车列表渲染
    renderCart: function() {
      var that = this;
      server
        .getCartChecked()
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.cartList = $data.data;
            that.calculateCartTotal();
          }
        })
        .catch(function(error, data) {
          
          // that.$message.error(error.response.data.message);
        });
    },

    // 计算购物车总额
    calculateCartTotal: function() {
      for (const key of this.cartList) {
          this.cartTotal += parseInt(key.goods_num) * parseFloat(key.member_price);
      }
    },
    
    // 提交订单
    submitOrder : function() {
      var that = this;
      var params = {};
      if(this.$route.addressId){
        params.address_id = this.$route.addressId;
      }
      server
        .submitOrder(params)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.$router.push(`/order/${$data.data.order_id}/pay`);
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    }
  }
};
</script>
